<template>
  <div class="main">
    <component :is="currentComponent"></component>
    <ToolBar @onChangeFragment="onChangeFragment"></ToolBar>
  </div>
</template>

<script>
import { useRouter, useRoute } from "vue-router";
import ToolBar from "@c/currency/TootBar.vue";
import { defineAsyncComponent } from "vue";
// import Home from '@c/Home';
// import Shopping from '@c/Shopping';
// import My from '@c/My';

export default {
  name: "MainView",
  components: {
    ToolBar,
    home: defineAsyncComponent(() => import("@c/Home.vue")),
    shopping: defineAsyncComponent(() => import("@c/Shopping.vue")),
    my: defineAsyncComponent(() => import("@c/My.vue")),
  },
  methods: {
    onChangeFragment(componentName) {
      this.currentComponent = componentName;
    },
  },
  data() {
    return {
      currentComponent: "home",
    };
  },
  setup() {
    const router = useRouter(),
      route = useRoute();
    const handleGoods = () => {
      const goods = route.query.to ? route.query.to : 0;
      return goods;
    };
    return { handleGoods };
  },
  activated() {
    //   if (this.handleGoods()) {
    //     this.currentComponent = "shopping";
    //   }
    //   console.log(this.handleGoods());
  },
};
</script>

<style lang="scss" scoped>
.main {
  position: absolute;

  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  component {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
}
</style>
