<template >
    <div class="tool-bar">
        <div class="tool-bar-item" v-for="(item,index) in toolBarData" :key="index" @click="onItemClick(item, index)">
          <img class="tool-bar-item-img" :src="[index === selectIndex ? item.hIcon : item.nIcon]">
            <p class="tool-bar-item-name" :class="[index === selectIndex ? 'tool-bar-item-name-h' : '']">{{item.name}}</p>
        </div>
    </div>
</template>

<script>
export default { 
  data () {
    return {
      //三个按钮数据源
      toolBarData: [
        {
          nIcon: require('@img/home-n.svg'),
          hIcon: require('@img/home-h.svg'),
          name: '首页',
          componentName: 'home'
        },
        {
          nIcon: require('@img/shopping-n.svg'),
          hIcon: require('@img/shopping-h.svg'),
          name: '购物车',
          componentName: 'shopping'
        },
        {
          nIcon: require('@img/my-n.svg'),
          hIcon: require('@img/my-h.svg'),
          name: '我的',
          componentName: 'my'
        }
      ],
      selectIndex: 0,
      
    }
  },
  methods: {
        onItemClick (item, index) {
            this.selectIndex = index;
            this.$emit('onChangeFragment', item.componentName);
        },
        onChangeComponent (index) {
            this.onItemClick(this.toolBarData[index], index);
        }
    }
}
</script>


<style lang="scss" scoped>
@import '@css/style.scss';
    .tool-bar {
        width: 100%;
        height: px2rem(46);
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        background-color: white;
        box-shadow: 0 0 16px 0 rgba(0, 0, 0, 0.2);
        border-top: 1px solid $lineColor;
      
        &-item {
            text-align: center;
            padding: px2rem(4) px2rem(12);
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            &-img {
                width: px2rem(22);
                height: px2rem(22);
            }
            &-name {
                font-size: $infoSize;
                margin-top: px2rem(4);
                &-h {
                    color: $mainColor;
                }
            }
            
        }
    }
</style>